.amir {
  position: absolute;
  /* margin: auto; */
  top: 50%;
  left: 40%;
  right: 0;
  bottom: 0;
  width: 300px;
  height: 100px;
}
.amir .search {
  position: absolute;
  margin: auto;
  top: -90px;
  right: 0;
  bottom: 0;
  left: 0;
  width: 80px;
  height: 80px;
  background: BLACK;
  border-radius: 50%;
  -webkit-transition: all 1s;
  transition: all 1s;
  z-index: 4;
  box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.4);
}
.amir .search:hover {
  cursor: pointer;
}
.amir .search::before {
  content: "";
  position: absolute;
  margin: auto;
  top: 22px;
  right: 0;
  bottom: 0;
  left: 22px;
  width: 12px;
  height: 2px;
  background: white;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  -webkit-transition: all .5s;
  transition: all .5s;
}
.amir .search::after {
  content: "";
  position: absolute;
  margin: auto;
  top: -5px;
  right: 0;
  bottom: 0;
  left: -5px;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: 2px solid white;
  -webkit-transition: all .5s;
  transition: all .5s;
}
.amir input {
  font-family: 'Inconsolata', monospace;
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 50px;
  height: 50px;
  outline: none;
  border: none;
  background: green;
  color: black;
  text-shadow: 0 0 10px white;
  padding: 0 80px 0 20px;
  border-radius: 30px;
  box-shadow: 0 0 25px 0 grey, 0 20px 25px 0 rgba(0, 0, 0, 0.2);
  -webkit-transition: all 1s;
  transition: all 1s;
  opacity: 0;
  z-index: 5;
  font-weight: bolder;
  letter-spacing: 0.1em;
}
.amir input:hover {
  cursor: pointer;
}
.amir input:focus {
  width: 300px;
  opacity: 1;
  cursor: text;
}
.amir input:focus ~ .search {
  right: -250px;
  background: #151515;
  z-index: 6;
}
.amir input:focus ~ .search::before {
  top: 0;
  left: 0;
  width: 25px;
}
.amir input:focus ~ .search::after {
  top: 0;
  left: 0;
  width: 25px;
  height: 2px;
  border: none;
  background: white;
  border-radius: 0%;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.amir input::-webkit-input-placeholder {
  color: black;
  opacity: 1;
  font-weight: bolder;
}
.amir input:-ms-input-placeholder {
  color: black;
  opacity: 1;
  font-weight: bolder;
}
.amir input::-ms-input-placeholder {
  color: black;
  opacity: 1;
  font-weight: bolder;
}
.amir input::placeholder {
  color: black;
  opacity: 1;
  font-weight: bolder;
}

