.modal-wrapper {
  background: rgba(0, 0, 0, 0.315);
  border: 1px solid #f1eeee;
  box-shadow: 0 5px 8px 0 rgba(0,0,0,0.2), 0 7px 20px 0 rgba(0,0,0,0.17);
  margin: 10px auto 0;
  transition: all .8s;
  width: 30%;
  top:200px;
  left :420px;  
  position: absolute;
  z-index: 100;
}
.back{
  width: 100%;
  height: 100%;
  position: relative;
  left: 0;
  top: 30px;
  background-color:black;
  opacity: 0.1;
}

.modal-header {
  background: black;
  height: 40px;
  line-height: 40px;
  padding: 5px 20px;
  text-align: center;
}

.modal-header h3 {
  color: white;
  float: left;
  margin: 0;
  padding: 0;
}

.modal-body {
  padding: 10px 15px;
  text-align: center;
  background: white
}

.modal-footer {
  background: black;
  height: 35px;
  padding: 15px;
}

.close-modal-btn {
  color: white;
  cursor: pointer;
  float: right;
  font-size: 30px;
  margin: 0;
}

.close-modal-btn:hover {
  color: red;
}

.btn-cancel, .btn-continue {
  background: black;
  border: none;
  color: white;
  cursor: pointer;
  font-weight: bold;
  outline: none;
  padding: 10px;
}

.btn-cancel {
  background-color: #b71c1c;
  float: right;
}
@media(max-width:500px){
  .btn-cancel{
  width:80px

}
}

.btn-continue {
  background-color: #1b5e20;
  float: left;
}

.back-drop {
  background-color: rgba(48, 49, 48, 0.42);
  height: 100%;
  position: relative;
  transition: all 1.3s;
  z-index: 100;
  width: 100%;
}

.open-modal-btn {
  margin: 15px;
  padding: 10px;
  font-weight: bold;
}